html {
  font-family: arial;
  background: rgb(29, 29, 30);
  display: flex;
  justify-content: center;
  margin-top: 50px;
}
html > * {
  margin: 0px;
}

.appContainer {
  width: 550px;
  background: rgb(43, 41, 48);
}

.headerContainer {
  background: rgb(71, 69, 80);
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 20px;
}
.headerContainer svg {
  fill: rgb(222, 215, 247);
}
.headerContainer h1 {
  color: rgb(222, 215, 247);
  font-size: 20px;
  margin-left: 15px;
  font-weight: lighter;
}

.entriesArrayContainer {
  overflow-y: scroll;
  max-height: 600px;
}
.entriesArrayContainer::-webkit-scrollbar {
  width: 10px;
}
.entriesArrayContainer::-webkit-scrollbar-track {
  background: none;
}
.entriesArrayContainer::-webkit-scrollbar-thumb {
  background: rgb(103, 103, 103);
  border-radius: 10px;
  border: solid 2px transparent;
}
.entriesArrayContainer::-webkit-scrollbar-thumb:hover {
  background: grey;
}

.entryContainer {
  margin: 20px 40px;
  display: grid;
  grid-template-columns: auto auto;
  padding-bottom: 15px;
  border-bottom: solid 1px rgb(29, 28, 32);
}
.entryContainer .entryImageContainer img {
  width: 125px;
  height: 168px;
  border-radius: 5px;
  -o-object-fit: cover;
     object-fit: cover;
}
.entryContainer .entryInfoContainer {
  margin: 19px;
}
.entryContainer .entryInfoContainer .entryLocationInfo {
  display: flex;
  align-items: center;
}
.entryContainer .entryInfoContainer .entryLocationInfo > * {
  margin-right: 10px;
}
.entryContainer .entryInfoContainer .entryLocationInfo .entryLocationCountry {
  color: white;
  text-transform: uppercase;
  font-size: 10px;
  letter-spacing: 2px;
}
.entryContainer .entryInfoContainer .entryLocationInfo .entryLocationGMAPLink {
  color: grey;
  font-size: 9px;
}
.entryContainer .entryInfoContainer .entryLocationTitle {
  font-size: 25px;
  margin: 5px 0;
  margin-bottom: 10px;
  color: rgb(222, 215, 247);
}
.entryContainer .entryInfoContainer .entryDateRange {
  color: rgb(222, 215, 247);
  margin: 10px 0px;
}
.entryContainer .entryInfoContainer .entryDescription {
  font-size: 10.24px;
  color: rgb(201, 199, 208);
}/*# sourceMappingURL=index.css.map */