html{
    font-family: arial;
    background: rgb(29, 29, 30);
    > * {
        margin: 0px;
    }
    display: flex;
    justify-content: center;
    margin-top: 50px;
}
.appContainer{
    width: 550px;
    background: rgb(43, 41, 48);
}
.headerContainer{
    background: rgb(71, 69, 80);
    display: flex;
    justify-content: center;
    align-items: center;
    padding: 20px;
    svg{
        fill: rgb(222, 215, 247);
    }
    h1{
        color: rgb(222, 215, 247);
        font-size: 20px;
        margin-left: 15px;
        font-weight: lighter;
    }
}
.entriesArrayContainer{
    overflow-y: scroll;
    max-height: 600px;
    &::-webkit-scrollbar{
        width: 10px;
    }
    &::-webkit-scrollbar-track{
        background: none;
    }
    &::-webkit-scrollbar-thumb{
        background: rgb(103, 103, 103);
        border-radius: 10px;
        border: solid 2px transparent;
        &:hover{
            background: grey;
        }
    }
}
.entryContainer{
    margin: 20px 40px;
    display: grid;
    grid-template-columns: auto auto;
    padding-bottom: 15px;
    border-bottom: solid 1px rgb(29, 28, 32);
    .entryImageContainer{
        img{
            width: 125px;
            height: 168px;
            border-radius: 5px;
            object-fit: cover;
        }
    }
    .entryInfoContainer{
        margin: 19px;
        .entryLocationInfo{
            display: flex;
            align-items: center;
            > *{
                margin-right: 10px;
            }
            .entryLocationCountry{
                color: white;
                text-transform:uppercase;
                font-size: 10px;
                letter-spacing: 2px;
            }
            .entryLocationGMAPLink{
                color: grey;
                font-size: 9px;
            }
        }
        .entryLocationTitle{
            font-size: 25px;
            margin: 5px 0;
            margin-bottom: 10px;
            color: rgb(222, 215, 247);
        }
        .entryDateRange{
            color: rgb(222, 215, 247);
            margin: 10px 0px;
        }
        .entryDescription{
            font-size: 10.24px;
            color: rgb(201, 199, 208);
        }
    }
}
    